<template>
  <div :style="isShow==true?[blue]:[]">
    <!-- <audio src="../assets/music.m4a" autoplay="autoplay"></audio> -->
    <!-- <img src="../assets/logo.png"
         alt> -->
    <!-- <checkbox></checkbox> -->
    <slot></slot>
    <input
      type="text"
      v-model="message"
      @mouseover="show"
    >
    <div class="aaa">输入框输入{{msg}}：{{message}}</div>
  </div>
</template>

<script>
import checkbox from './Checkbox.vue'
export default {
  props: {
    msg: {
      // required: true,
      // validator: function(value) {
      //   return ['success', 'warning', 'danger'].indexOf(value) !== -1
      // },
      // default: 'opaaap'
    }
  },
  data() {
    return {
      message: '',
      isShow: false,
      blue: {
        backgroundColor: 'red'
      }
    }
  },
  methods: {
    show() {
      this.$emit('sontofather', '子传父')
      this.bus.$emit('brothermsg', this.message)
    }
  },
  components: {
    checkbox
  }
}
</script>

<style scoped lang="less">
@color: #923f2a;
.aaa {
  color: @color;
}
</style>



