<template>
  <div style="background-color:yellow">
    <!-- <mt-swipe :auto="1000">
      <mt-swipe-item>
        <img src="../assets/1.jpg">
      </mt-swipe-item>
      <mt-swipe-item>
        <img src="../assets/2.jpg">
      </mt-swipe-item>
      <mt-swipe-item>
        <img src="../assets/3.jpg">
      </mt-swipe-item>
    </mt-swipe>-->
    <div
      v-for="(item,index) in list"
      :key="index"
      :data-num="item"
      ref="ooo"
      @click="show($event,index)"
    >{{item}}</div>
    <div
      id="main"
      style="width: 600px;height: 400px;"
    ></div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      list: ["a", "b", "c"],
      charts: "",
      opinion: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"],
      opinionData: [
        { value: 335, name: "直接访问" },
        { value: 310, name: "邮件营销" },
        { value: 234, name: "联盟广告" },
        { value: 135, name: "视频广告" },
        { value: 1548, name: "搜索引擎" }
      ]
    };
  },
  components: {},
  methods: {
    show(e, index) {
      // console.log(this.$refs.ooo[e].dataset.num);
      // console.log(e.target.dataset.num);
      // console.log(this.$refs);
    },
    drawPie(id) {
      this.charts = echarts.init(document.getElementById(id));
      this.charts.setOption({
        tooltip: {
          trigger: "item",
          formatter: "{a}<br/>{b}:{c} ({d}%)"
        },
        legend: {
          orient: "vertical",
          x: "left",
          data: this.opinion
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "blod"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: this.opinionData
          }
        ]
      });
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.drawPie("main");
    });
  }
};
</script>

<style scoped>
.mint-swipe {
  height: 200px;
}
.mint-swipe .mint-swipe-item img {
  width: 100%;
  height: 100%;
}
</style>


